import Vue from "vue";
import Router from "vue-router";

const routerOptions = [
  { path: "/", page: "NFTView" },
  { path: "*", page: "NotFound" }
];

const routes = routerOptions.map(route => {
  return {
    ...route,
    component: () => import(`../pages/${route.page}.vue`)
  };
});

Vue.use(Router);

export default new Router({
  mode: "history",
  routes
});



